const jsonLdFaqSchema = {
  '@context': 'https://schema.org',
  '@type': 'FAQPage',
  mainEntity: [
    {
      '@type': 'Question',
      name: 'What is FAQPage Schema?',
      acceptedAnswer: {
        '@type': 'Answer',
        text:
          "<p>The FAQPage structured data indicates that the page is considered to have a section with Frequently Asked Question (FAQ) and answers to those questions.</p><p>Schema.org has more info on FAQ structured data and at a Google Dance event in Singapore in <a href='https://twitter.com/akhil1594/status/1022393493761622016' rel='noopener noreferrer ' target='_blank'>July 2018</a> Google announced they will start using this in search. You can find Google&#39;s documentation <a href='https://developers.google.com/search/docs/data-types/faqpage' rel='noopener noreferrer ' target='_blank'>here</a>.</p>",
      },
    },
    {
      '@type': 'Question',
      name: 'How can I get FAQ rich results in Google?',
      acceptedAnswer: {
        '@type': 'Answer',
        text:
          "<p>Google has started showing pages with FAQ structured data markup (Microdata or JSON-LD) with a rich result in SERPs (search engine result pages) and also reads out your answer on assistant powered devices(limited to Android phones for now) with <a href='https://developers.google.com/actions/content-actions/faq'    rel='noopener noreferrer ' target='_blank'>Markup Action</a> for the Google Assistant.</p><p>You do not need any coding experience to use this tool. The video will give you more info on how to use this FAQPage Schema Generator.</p>",
      },
    },
    {
      '@type': 'Question',
      name: 'Should I choose Prettified OR Minified version?',
      acceptedAnswer: {
        '@type': 'Answer',
        text:
          '<p>The tool will give you two version of the FAQPage JSON-LD Schema code. Either one should work but the minified version has a small advantage when it comes to the performance of your website. That is what I would recommend you use.</p>',
      },
    },
    {
      '@type': 'Question',
      name: 'Will emojis and HTML tags work with FAQPage Schema?',
      acceptedAnswer: {
        '@type': 'Answer',
        text:
          "<p>Google does seem to accept things like emoji and unicode characters like <a href='https://saijogeorge.com/strikethrough-text-generator/' rel='noopener noreferrer ' target='_blank'>strikethrough text</a> but according to Google <strong>officially</strong>    the answer section can contain these HTML tags:&nbsp;</p><ul><li>headings - h1, h2, h3, h4, h5, h6</li><li>links - a</li><li>line breaks - br</li><li>lists - ol, ul, li</li><li>paragraph - p, div</li><li>bold - b, strong</li><li>italics - i, em</li></ul>",
      },
    },
    {
      '@type': 'Question',
      name: 'Does FAQ rich result work on mobile or desktop?',
      acceptedAnswer: {
        '@type': 'Answer',
        text:
          '<p>FAQ rich results will work with desktop 🖥 and mobile 📱 search results across all Google supported countries and languages.</p>',
      },
    },
    {
      '@type': 'Question',
      name:
        'How can I get FAQPage to work with Google assistant powered devices?',
      acceptedAnswer: {
        '@type': 'Answer',
        text:
          "<p>The FAQPage code generated by this FAQ Page Schema Generator tool will be picked up by both Google Search and Google Assistant powered devices.</p><p>You just need to install it on your page, you can check if it&#39;s working as expected by validating the page with Google&rsquo;s <a href='https://search.google.com/test/rich-results' rel='noopener noreferrer ' target='_blank'>Rich Result Testing</a>    tool. You don&#39;t need to do anything extra for it to be picked up by Google assistant powered devices.</p>",
      },
    },
    {
      '@type': 'Question',
      name: 'What is the difference between FAQ and QA Schema Markup?',
      acceptedAnswer: {
        '@type': 'Answer',
        text:
          "<p>FAQ and QA are two different structured data markups that can be used on your page to get rich results in Google search results. While they sound similar they have a slightly different purpose.</p><p>If you offer a page where multiple users can submit alternative answers to a question think sites like Quora, StackOverflow etc then you have to use <a href='https://developers.google.com/search/docs/data-types/qapage' rel='noopener noreferrer ' target='_blank'>Q&nbsp;A Schema</a>.</p><p>If you have a FAQ page with questions and a specific answer to those questions around the same topic you will have to use FAQ Schema. eg: this page uses <a href='https://developers.google.com/search/docs/data-types/faqpage' rel='noopener noreferrer '    target='_blank'>FAQ Schema</a>.</p>",
      },
    },
    {
      '@type': 'Question',
      name: 'What is the difference between FAQ and FAQPAge Schema Markup?',
      acceptedAnswer: {
        '@type': 'Answer',
        text:
          "<p>AFAIK they are the same thing. Schema.org calls it <a href='https://schema.org/FAQPage' rel='noopener noreferrer ' target='_blank'>FAQPage</a> and <a href='https://developers.google.com/search/docs/data-types/faqpage' rel='noopener noreferrer ' target='_blank'>Google</a>    refers to it as both FAQ and FAQPages.</p><p>Some other common names for this are FAQ Schema, FAQ Structured Data, FAQ JSON-LD, etc.</p>",
      },
    },
    {
      '@type': 'Question',
      name: 'Looking for FAQ schema markup example?',
      acceptedAnswer: {
        '@type': 'Answer',
        text:
          '<p>Here is the FAQ Schema markup used on this page, you can also see this in Google by searching for &quot;Saijo FAQ Schema Generator&quot;.</p>',
      },
    },
  ],
}

