const Label = styled.label({
  color: theme.colors.grey_400,
})

const formatCode = (value, type) => {
  switch (type) {
    default:
    case 'prettified':
      return `<script type="application/ld+json">\n${JSON.stringify(
        value,
        null,
        2
      )}\n</script>\n<!--FAQPage Code Generated by https://saijogeorge.com/json-ld-schema-generator/faq/-->`
    case 'minified':
      return `<script type="application/ld+json">${JSON.stringify(
        value
      )}</script><!--FAQPage Code Generated by https://saijogeorge.com/json-ld-schema-generator/faq/-->`
  }
}

const reorder = (list, startIndex, endIndex) => {
  const result = Array.from(list)
  const [removed] = result.splice(startIndex, 1)
  result.splice(endIndex, 0, removed)

  return result
}

const generateFaqSchema = faqs => ({
  '@context': 'https://schema.org',
  '@type': 'FAQPage',
  mainEntity: faqs.map(({ q, a }) => ({
    '@type': 'Question',
    name: q,
    acceptedAnswer: { '@type': 'Answer', text: a },
  })),
})

const Faq = React.forwardRef(
  (
    {
      as: As = 'div',
      questionRef,
      index,
      faq,
      faqs,
      setFaqs,
      dragHandleProps,
      isDragging,
      ...props
    },
    ref
  ) => (
    <As
      ref={ref}
      css={{
        display: 'flex',
        marginBottom: rem(10),
        background: '#f9f9f9',
        borderRadius: 6,
        border: '1px solid #eee',
      }}
      {...props}
    >
      <div
        css={{
          display: 'flex',
          alignItems: 'center',
          paddingRight: rem(5),
          paddingLeft: rem(5),
          ':active svg': { fill: theme.colors.primary },
        }}
        {...dragHandleProps}
      >
        <svg
          viewBox="0 0 24 24"
          css={{
            width: rem(24),
            height: rem(24),
            transition: 'fill .6s',
            fill: '#ddd',
          }}
        >
          <path d="M9,3H11V5H9V3M13,3H15V5H13V3M9,7H11V9H9V7M13,7H15V9H13V7M9,11H11V13H9V11M13,11H15V13H13V11M9,15H11V17H9V15M13,15H15V17H13V15M9,19H11V21H9V19M13,19H15V21H13V19Z" />
        </svg>
      </div>
      <div css={{ flex: 1, paddingTop: rem(20), paddingBottom: rem(20) }}>
        <TextArea
          ref={questionRef}
          placeholder={`Question #${index + 1}`}
          autosize={{ minRows: 1, maxRows: 3 }}
          value={faq.q}
          onChange={e => {
            const newFaqs = Array.from(faqs)
            newFaqs[index] = { ...newFaqs[index], q: e.target.value }
            setFaqs(newFaqs)
          }}
          css={{ resize: 'none' }}
        />
        <Space y={10} />
        <TextArea
          placeholder={`Answer #${index + 1}`}
          autosize={{ minRows: 2, maxRows: 6 }}
          value={faq.a}
          onChange={e => {
            const newFaqs = Array.from(faqs)
            newFaqs[index] = { ...faqs[index], a: e.target.value }
            setFaqs(newFaqs)
          }}
          css={{ resize: 'none' }}
        />
      </div>
      <button
        css={{
          paddingRight: rem(9),
          paddingLeft: rem(8),
          ':hover svg, :focus svg': { fill: theme.colors.blue },
        }}
        onClick={e => {
          const newFaqs = Array.from(faqs)
          newFaqs.splice(index, 1)
          setFaqs(newFaqs)
        }}
      >
        <svg
          viewBox="0 0 24 24"
          css={{
            width: rem(17),
            height: rem(17),
            fill: '#ddd',
            transition: '.2s',
          }}
        >
          <path d="M9,3V4H4V6H5V19A2,2 0 0,0 7,21H17A2,2 0 0,0 19,19V6H20V4H15V3H9M7,6H17V19H7V6M9,8V17H11V8H9M13,8V17H15V8H13Z" />
        </svg>
      </button>
      {/* <button
      css={{
        alignSelf: 'flex-start',
        margin: '3px 3px -1px -1px',
      }}
      onClick={e => {
        const newFaqs = Array.from(faqs)
        newFaqs.splice(index, 1)
        setFaqs(newFaqs)
      }}
    >
      <svg
        viewBox="0 0 24 24"
        css={{
          width: rem(17),
          height: rem(17),
          fill: '#ddd',
          transition: '.2s',
          ':hover': { fill: theme.colors.blue },
        }}
      >
        <path d="M19,6.41L17.59,5L12,10.59L6.41,5L5,6.41L10.59,12L5,17.59L6.41,19L12,13.41L17.59,19L19,17.59L13.41,12L19,6.41Z" />
      </svg>
    </button> */}
    </As>
  )
)

const useEditorState = createEditorState('faq__draft')

const Form = ({ navigate }) => {
  const lastItem = useRef()
  const [faqs, setFaqs] = useEditorState(null, [{ q: '', a: '' }])
  // const [output, setOutput] = useState('')
  const [outputType, setOutputType] = useState('minified')
  const formattedOutput = formatCode(generateFaqSchema(faqs), outputType)

  return (
    <div
      css={{
        display: 'grid',
        gridTemplateColumns: '1fr',
        gridGap: rem(20),
        padding: rem(20),
        [media.tabletLg]: {
          gridTemplateColumns: '1fr 1fr',
          gridGap: rem(30),
          padding: rem(30),
        },
        [min(1200)]: {
          gridGap: rem(40),
          padding: rem(40),
        },
        background: '#fff',
        borderRadius: 8,
        boxShadow: '0 5px 30px #CBD6D9',
      }}
    >
      <div>
        <Label>FAQs</Label>
        <Space y={10} />
        <DragDropContext
          onDragStart={() => {
            document.activeElement.blur()
          }}
          onDragEnd={result => {
            if (!result.destination) return

            setFaqs(
              reorder(faqs, result.source.index, result.destination.index)
            )
          }}
        >
          <Droppable droppableId="droppable">
            {provided => (
              <ul ref={provided.innerRef} {...provided.droppableProps}>
                {faqs.map((faq, index) => (
                  <Draggable
                    key={`faq-${index}`}
                    draggableId={`faq-${index}`}
                    index={index}
                  >
                    {(provided, snapshot) => (
                      <Faq
                        as="li"
                        key={index}
                        questionRef={
                          index === faqs.length - 1 ? lastItem : null
                        }
                        index={index}
                        faq={faq}
                        faqs={faqs}
                        setFaqs={setFaqs}
                        ref={provided.innerRef}
                        {...provided.draggableProps}
                        isDragging={snapshot.isDragging}
                        dragHandleProps={provided.dragHandleProps}
                      />
                    )}
                  </Draggable>
                ))}
                {provided.placeholder}
              </ul>
            )}
          </Droppable>
        </DragDropContext>
        <Button
          css={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            width: '100%',
            marginTop: rem(20),
          }}
          onClick={() => {
            setFaqs([...faqs, { q: '', a: '' }])
            setTimeout(() => lastItem.current.focus())
          }}
        >
          <svg
            viewBox="0 0 24 24"
            css={{
              width: rem(18),
              height: rem(18),
              marginRight: rem(4),
              fill: 'currentColor',
            }}
          >
            <path d="M19,13H13V19H11V13H5V11H11V5H13V11H19V13Z" />
          </svg>
          Add another FAQ
        </Button>
        {/* <Space y={20} />
        <Button
          type="primary"
          size="default"
          onClick={() => {
            setOutput(generateFaqSchema(faqs))
            navigate('faq#output')
          }}
        >
          Generate JSON-LD FAQ Schema
        </Button> */}
      </div>
      <div
        id="output"
        css={{
          alignSelf: 'flex-start',
          position: 'sticky',
          top: 0,
          marginTop: rem(-14),
        }}
      >
        <Label
          htmlFor="output-code"
          css={{
            display: 'inline-block',
            paddingTop: rem(14),
          }}
        >
          JSON-LD FAQ Schema Code
        </Label>
        <Space y={10} />
        <TextArea
          id="output-code"
          autosize={{ minRows: 5 }}
          value={formattedOutput}
          // disabled={!output}
          readOnly
          css={{
            maxHeight: 'calc(100vh - 150px) !important',
            fontFamily: 'monospace',
            resize: 'none',
            '&.ant-input': { minHeight: '135px !important' },
          }}
        />
        <Space y={20} />
        <div
          css={{
            display: 'flex',
            flexWrap: 'wrap',
            // justifyContent: 'space-between',
            alignItems: 'center',
            marginTop: -15,
            marginLeft: -15,
            '> *': {
              marginTop: 15,
              marginLeft: 15,
            },
          }}
        >
          <Button
            type="primary"
            size="default"
            // disabled={!output}
            onClick={() => {
              copyToClipboard(formattedOutput)
              message.success('Copied to clipboard!')
            }}
          >
            Copy FAQ Schema
          </Button>
          <form
            method="post"
            target="_blank"
            action="https://search.google.com/structured-data/testing-tool"
          >
            <Button
              type="primary"
              size="default"
              // disabled={!output}
              htmlType="submit"
            >
              Test in SDTT
            </Button>
            <textarea
              name="code"
              value={formattedOutput}
              readOnly
              css={{ display: 'none' }}
            />
          </form>
          <Link to="/tag-manager-fix/" state={{ code: formattedOutput }}>
            <Button
              type="primary"
              size="default"
              // disabled={!output}
            >
              Send to Tag Manager Fixer
            </Button>
          </Link>
          <div
            css={{
              // minHeight: rem(37),
              color: theme.colors.grey_200,
            }}
          >
            <Radio.Group
              value={outputType}
              onChange={e => setOutputType(e.target.value)}
              size="large"
              // disabled={!output}
            >
              <Radio value="minified">Minified</Radio>
              <Radio value="prettified">Prettified</Radio>
            </Radio.Group>
            <a
              href="#should-i-choose-prettified-or-minified-version"
              title="Should I choose Prettified OR Minified version?"
            >
              (What is this?)
            </a>
          </div>
        </div>
      </div>
    </div>
  )
}

const slug = 'faq'
const url = encodeURIComponent(
  `${siteConfig.siteUrl}${siteConfig.pathPrefix}/${slug}`
)
const title = 'FAQPage JSON-LD Schema Generator'
const description =
  'Easy to use FAQ Schema Generator for SEOs, no coding experience required to use these. Use it on your phone or desktop and works on WordPress, WIX, Shopify, etc.'

const IndexPage = ({ navigate }) => {
  const data = useStaticQuery(graphql`
    {
      avatar: file(relativePath: { eq: "saijo-george.png" }) {
        childImageSharp {
          fluid(maxWidth: 44, quality: 80) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
      faqPageSchemaGenerator: file(
        relativePath: { eq: "faq-page-schema-generator.png" }
      ) {
        childImageSharp {
          fluid(maxWidth: 570, quality: 80) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
      jsonLdTagManagerFix: file(
        relativePath: { eq: "json-ld-tag-manager-fix.png" }
      ) {
        childImageSharp {
          fluid(maxWidth: 570, quality: 80) {
            ...GatsbyImageSharpFluid_withWebp
          }
        }
      }
      facebookImage: file(
        relativePath: { eq: "faq-page-schema-generator.png" }
      ) {
        ...facebookShareImage
      }
      twitterImage: file(
        relativePath: { eq: "faq-page-schema-generator.png" }
      ) {
        ...twitterShareImage
      }
    }
  `)

  return (
    <Layout title={title}>
      <SEO
        title={title}
        description={description}
        facebookImage={data.facebookImage.childImageSharp.fixed.src}
        twitterImage={data.twitterImage.childImageSharp.fixed.src}
      />
      <Container>
        <Space y={{ mobile: 15, tabletMd: 50 }} />
        <div
          css={{
            display: 'grid',
            gridTemplateColumns: '1fr',
            [media.tabletMd]: { gridTemplateColumns: '1fr auto' },
            gridGap: 30,
          }}
        >
          <div>
            <a
              href="https://saijogeorge.com"
              css={{
                display: 'none',
                [media.max.tablet]: { display: 'flex' },
                alignItems: 'center',
                color: 'rgba(0, 0, 0, 0.85) !important',
                marginBottom: 20,
              }}
            >
              <Img
                fluid={data.avatar.childImageSharp.fluid}
                alt="SaijoGeorge"
                css={{
                  width: 30,
                  height: 30,
                  marginRight: 15,
                  borderRadius: '50%',
                  filter: 'grayscale(1)',
                  transition: '.3s',
                  'a:hover &': {
                    transform: 'scale(1.65)',
                    filter: 'grayscale(0)',
                  },
                }}
              />
              <Paragraph>Created by Saijo George</Paragraph>
            </a>
            <script type="application/ld+json">
              {JSON.stringify(jsonLdBreadcrumbSchema)}
            </script>
            <Paragraph css={{ maxWidth: '63em' }}>
              {'The FAQ Page Schema Generator is one of my new '}
              <Link
                out
                href="https://saijogeorge.com/json-ld-schema-generator/"
              >
                JSON-LD Schema Generators
              </Link>
              {
                ' that can help you quickly create the required structured data for your content to be eligible for display as a rich result in Google search. I have made these as simple as possible so you can generate structured data markup in a few simple clicks. You will not need any coding experience to use these and these tools should work on all mobile and desktop devices.'
              }
            </Paragraph>
            <Space y={20} />
            <Paragraph css={{ maxWidth: '63em' }}>
              {
                'If you enjoy using these generators please do consider leaving me a recommendation on '
              }
              <Link out href="https://au.linkedin.com/in/saijogeorge/">
                LinkedIn
              </Link>
              {
                ' and if you run into any issues please get in touch with me on '
              }
              <Link out href="http://www.twitter.com/SaijoGeorge/">
                Twitter
              </Link>
              {' 🤙.'}
            </Paragraph>
          </div>
          <div
            css={{
              display: 'flex',
              flexDirection: 'column',
              [media.tabletMd]: { alignItems: 'flex-end' },
            }}
          >
            <Paragraph>Share on social media</Paragraph>
            <Space y={10} />
            <SocialIcons horizontal />
          </div>
        </div>
        <Space y={{ mobile: 30, tabletMd: 70 }} />
        <Form navigate={navigate} />
        <Space y={{ mobile: 50, tabletMd: 100 }} />
        <div
          css={{
            display: 'grid',
            gridTemplateColumns: '1fr',
            gridGap: 30,
            [media.tabletMd]: { gridTemplateColumns: '1fr 1fr', gridGap: 60 },
            alignItems: 'center',
            '.gatsby-image-wrapper': {
              borderRadius: 8,
              boxShadow: '0 5px 30px #CBD6D9',
            },
          }}
        >
          <div>
            <Img
              fluid={data.faqPageSchemaGenerator.childImageSharp.fluid}
              alt="json-ld-faq-schema-generator"
            />
          </div>
          <script type="application/ld+json">
            {JSON.stringify(jsonLdFaqSchema)}
          </script>
          <div>
            <Title as="h2">What is FAQPage Schema?</Title>
            <Space y={20} />
            <Paragraph css={{ maxWidth: '55em' }}>
              The FAQPage structured data indicates that the page is considered
              to have a section with Frequently Asked Question (FAQ) and answers
              to those questions.
            </Paragraph>
            <Space y={20} />
            <Paragraph css={{ maxWidth: '55em' }}>
              {
                'Schema.org has more info on FAQ structured data and at a Google Dance event in Singapore in '
              }
              <Link
                out
                href="https://twitter.com/akhil1594/status/1022393493761622016"
              >
                July 2018
              </Link>
              {
                " Google announced they will start using this in search. You can find Google's documentation "
              }
              <Link
                out
                href="https://developers.google.com/search/docs/data-types/faqpage"
              >
                here
              </Link>
              .
            </Paragraph>
          </div>
        </div>
        <Space y={{ mobile: 50, tabletMd: 100 }} />
        <div
          css={{
            display: 'grid',
            gridTemplateColumns: '1fr',
            gridGap: 30,
            [media.tabletMd]: { gridTemplateColumns: '1fr 1fr', gridGap: 60 },
            alignItems: 'center',
            '.gatsby-image-wrapper': {
              borderRadius: 8,
              boxShadow: '0 5px 30px #CBD6D9',
            },
          }}
        >
          <div>
            <Title as="h2">How can I get FAQ rich results in Google?</Title>
            <Space y={20} />
            <Paragraph>
              {
                'Google has started showing pages with FAQ structured data markup (Microdata or JSON-LD) with a rich result in SERPs (search engine result pages) and also reads out your answer on assistant powered devices(limited to Android phones for now) with '
              }
              <Link
                out
                href="https://developers.google.com/actions/content-actions/faq"
              >
                Markup Action
              </Link>
              {' for the Google Assistant.'}
            </Paragraph>
            <Space y={20} />
            <Paragraph>
              You do not need any coding experience to use this tool. The video
              will give you more info on how to use this FAQPage Schema
              Generator.
            </Paragraph>
          </div>
          <div>
            <StaticVideo
              src={video}
              // autoPlay
              // muted
              // loop
              playsInline
              controls
              ratio={9 / 16}
              css={{
                borderRadius: 8,
                boxShadow: '0 5px 30px #CBD6D9',
              }}
            />
          </div>
        </div>
        <Space y={{ mobile: 50, tabletMd: 100 }} />
        <div css={{ maxWidth: 700, margin: 'auto' }}>
          <div id="should-i-choose-prettified-or-minified-version">
            <Title as="h2">
              Should I choose Prettified OR Minified version?
            </Title>
            <Space y={20} />
            <Paragraph>
              The tool will give you two version of the FAQPage JSON-LD Schema
              code. Either one should work but the minified version has a small
              advantage when it comes to the performance of your website. That
              is what I would recommend you use.
            </Paragraph>
          </div>
          <Space y={{ mobile: 50, tabletMd: 100 }} />
          <div>
            <Title as="h2">
              Will emojis and HTML tags work with FAQPage Schema?
            </Title>
            <Space y={20} />
            <Paragraph>
              {
                'Google does seem to accept things like emoji and unicode characters like '
              }
              <Link
                out
                href="https://saijogeorge.com/strikethrough-text-generator/"
              >
                strikethrough text
              </Link>
              {' but according to Google '}
              <b>officially</b>
              {' the answer section can contain these HTML tags:'}
            </Paragraph>
            <Space y={20} />
            <Paragraph>
              <ul>
                <li>headings - h1, h2, h3, h4, h5, h6</li>
                <li>links - a</li>
                <li>line breaks - br</li>
                <li>lists - ol, ul, li</li>
                <li>paragraph - p, div</li>
                <li>bold - b, strong</li>
                <li>italics - i, em</li>
              </ul>
            </Paragraph>
          </div>
          <Space y={{ mobile: 50, tabletMd: 100 }} />
          <div>
            <Title as="h2">
              Does FAQ rich result work on mobile or desktop?
            </Title>
            <Space y={20} />
            <Paragraph>
              {'FAQ rich results will work with desktop '}
              <span role="img" aria-label="desktop">
                🖥
              </span>
              {' and mobile '}
              <span role="img" aria-label="mobile">
                📱
              </span>
              {
                ' search results across all Google supported countries and languages.'
              }
            </Paragraph>
          </div>
          <Space y={{ mobile: 50, tabletMd: 100 }} />
          <div>
            <Title as="h2">
              How can I get FAQPage to work with Google assistant powered
              devices?
            </Title>
            <Space y={20} />
            <Paragraph>
              The FAQPage code generated by this FAQ Page Schema Generator tool
              will be picked up by both Google Search and Google Assistant
              powered devices.
            </Paragraph>
            <Space y={20} />
            <Paragraph>
              {
                "You just need to install it on your page, you can check if it's working as expected by validating the page with Google’s "
              }
              <Link out href="https://search.google.com/test/rich-results">
                Rich Result Testing
              </Link>
              {
                " tool. You don't need to do anything extra for it to be picked up by Google assistant powered devices."
              }
            </Paragraph>
          </div>
          <Space y={{ mobile: 50, tabletMd: 100 }} />
          <div>
            <Title as="h2">
              What is the difference between FAQ and QA Schema Markup?
            </Title>
            <Space y={20} />
            <Paragraph>
              FAQ and QA are two different structured data markups that can be
              used on your page to get rich results in Google search results.
              While they sound similar they have a slightly different purpose.
            </Paragraph>
            <Space y={20} />
            <Paragraph>
              {
                'If you offer a page where multiple users can submit alternative answers to a question think sites like Quora, StackOverflow etc then you have to use '
              }
              <Link
                out
                href="https://developers.google.com/search/docs/data-types/qapage"
              >
                Q&nbsp;A Schema
              </Link>
              .
            </Paragraph>
            <Space y={20} />
            <Paragraph>
              {
                'If you have a FAQ page with questions and a specific answer to those questions around the same topic you will have to use FAQ Schema. eg: this page uses '
              }
              <Link
                out
                href="https://developers.google.com/search/docs/data-types/faqpage"
              >
                FAQ Schema
              </Link>
              .
            </Paragraph>
          </div>
          <Space y={{ mobile: 50, tabletMd: 100 }} />
          <div>
            <Title as="h2">
              What is the difference between FAQ and FAQPAge Schema Markup?
            </Title>
            <Space y={20} />
            <Paragraph>
              {'AFAIK they are the same thing. Schema.org calls it '}
              <Link out href="https://schema.org/FAQPage">
                FAQPage
              </Link>
              {' and '}
              <Link
                out
                href="https://developers.google.com/search/docs/data-types/faqpage"
              >
                Google
              </Link>
              {' refers to it as both FAQ and FAQPages.'}
            </Paragraph>
            <Space y={20} />
            <Paragraph>
              Some other common names for this are FAQ Schema, FAQ Structured
              Data, FAQ JSON-LD, etc.
            </Paragraph>
          </div>
          <Space y={{ mobile: 50, tabletMd: 100 }} />
          <div>
            <Title as="h2">Looking for FAQ schema markup example?</Title>
            <Space y={20} />
            <Paragraph>
              Here is the FAQ Schema markup used on this page, you can also see
              this in Google by searching for "Saijo FAQ Schema Generator".
            </Paragraph>
            <Space y={20} />
          </div>
        </div>
      </Container>
      <Space y={{ mobile: 50, tabletMd: 100 }} />
      <Container as="section">
        <CodeExamples codeExeamples={codeExeamples} />
      </Container>
      <Space y={{ mobile: 50, tabletMd: 100 }} />
      <div css={{ maxWidth: 700, margin: 'auto' }}>
        <Title as="h2">Testimonials for FAQ JSON-LD Generator</Title>
        <Space y={20} />
        <Paragraph>
          Here is what other SEOs had to say about this FAQ Schema Generator.
        </Paragraph>
        <Space y={20} />
      </div>
      <Container as="section">
        <Testimonials testimonials={testimonials} />
      </Container>
      <Space y={{ mobile: 50, tabletMd: 100 }} />
      <ProjectsSection showGenerators={true} />
    </Layout>
  )
}

export default IndexPage
