import { useState, useEffect } from 'react'
import createPersistedState from 'use-persisted-state'
import { Modal } from 'antd'

const { confirm } = Modal

const createEditorState = key => {
  const usePersistedState = createPersistedState(key)

  return (passedState, defaultState = '') => {
    const [state, setState] = useState(passedState || defaultState)
    const [draft, setDraft] = usePersistedState(null)

    const userSetState = nextState => {
      setState(nextState)
      setDraft(nextState)
    }

    useEffect(() => {
      if (passedState) return

      if (draft && draft !== 'undefined') {
        confirm({
          title: 'Do you want to continue to work on the latest draft?',
          okText: 'Yes',
          cancelText: 'No',
          onOk() {
            setState(draft)
          },
          onCancel() {
            setDraft(null)
          },
        })
      }
    }, [])

    return [state, userSetState]
  }
}

export default createEditorState
