import React, { useEffect, useRef } from 'react'
import { withPrefix } from 'gatsby'
import Swiper from 'swiper'
import 'swiper/dist/css/swiper.min.css'

import Link from '../components/Link'

import theme from '../styles/theme'

const Testimonial = ({
  quote,
  name,
  nameURL,
  position,
  positionURL,
  companyName,
  companyURL,
  avatar,
  ...props
}) => {
  return (
    <blockquote
      css={{
        display: 'flex',
        flexDirection: 'column',
        height: 'auto',
        textAlign: 'center',
        borderRadius: 6,
        border: '1px solid #DBE4EC',
        background: 'rgba(255, 255, 255, .9)',
      }}
      {...props}
    >
      <svg
        viewBox="0 0 62 59"
        css={{
          width: 56,
          height: 53,
          fill: theme.colors.primary,
          position: 'absolute',
          top: -32,
          left: 'calc(50% - 28px)',
        }}
      >
        <path
          d="M23.8623 10.8302C18.7235 11.7983 15.1115 15.5221 13.0261 22.0015C11.9835 25.204 11.4622 28.3692 11.4622 31.4971C11.4622 31.8695 11.4622 32.2047 11.4622 32.5026C11.5366 32.726 11.6111 33.2846 11.6856 34.1783H23.8623V58.5318H0.0673828V36.0774C0.0673828 25.055 2.26441 16.5648 6.65847 10.6067C11.0525 4.64869 16.7872 1.22281 23.8623 0.329102V10.8302ZM61.5098 10.8302C57.4136 11.5004 54.2856 13.8092 52.1258 17.7564C50.0405 21.7036 48.9979 26.2466 48.9979 31.3854C48.9979 31.8323 48.9979 32.2791 48.9979 32.726C49.0723 33.1728 49.184 33.6569 49.333 34.1783H61.5098V58.5318H37.6031V36.0774C37.6031 27.2148 39.465 19.3204 43.1888 12.3941C46.9125 5.39344 53.0195 1.37176 61.5098 0.329102V10.8302Z"
          fill="#1890FF"
        />
      </svg>

      <div
        css={{
          flex: 1,
          display: 'flex',
          alignItems: 'center',
          padding: `55px 25px ${74 / 2 + 30}px`,
          fontSize: 18,
          lineHeight: 1.7,
          color: '#58696d',
        }}
      >
        <p dangerouslySetInnerHTML={{ __html: quote }}></p>
      </div>
      <footer css={{ padding: '0 20px 30px', borderTop: '1px solid #DBE4EC' }}>
        <img
          src={withPrefix(avatar ? avatar : '/avatar-placeholder.png')}
          alt={name}
          css={{
            display: 'inline-block',
            width: 74,
            height: 74,
            marginTop: -74 / 2,
            marginBottom: 20 - 2,
            objectFit: 'cover',
            borderRadius: '50%',
            filter: avatar
              ? 'none'
              : 'sepia(1) hue-rotate(-184deg) brightness(.98)',
          }}
        />
        <p
          css={{
            marginBottom: 3,
            fontWeight: 700,
            fontSize: 18,
            color: 'rgba(0, 0, 0, 0.85)',
          }}
        >
          {nameURL ? (
            <Link out href={nameURL}>
              {name}
            </Link>
          ) : (
            name
          )}
        </p>
        <p css={{ fontSize: 14, color: 'rgba(0, 0, 0, 0.45)' }}>
          {positionURL ? (
            <Link out href={positionURL}>
              {position}
            </Link>
          ) : (
            position
          )}
          {' @ '}
          {companyURL ? (
            <Link out href={companyURL}>
              {companyName}
            </Link>
          ) : (
            companyName
          )}
        </p>
      </footer>
    </blockquote>
  )
}

const Testimonials = ({ testimonials }) => {
  const swiperContainer = useRef()

  useEffect(() => {
    const mySwiper = new Swiper(swiperContainer.current, {
      slidesPerView: 3,
      spaceBetween: 30,
      navigation: {
        nextEl: '.testimonials-button-next',
        prevEl: '.testimonials-button-prev',
      },
      pagination: {
        el: '.testimonials-pagination',
        clickable: true,
      },
      breakpoints: {
        600: {
          slidesPerView: 1,
        },
        1024: {
          slidesPerView: 2,
        },
      },
    })

    return () => {
      mySwiper.destroy()
    }
  }, [])

  return (
    <div css={{ position: 'relative' }}>
      <div
        ref={swiperContainer}
        className="swiper-container"
        css={{ paddingTop: 50, paddingBottom: 50 }}
      >
        <div className="swiper-wrapper" css={{ display: 'flex' }}>
          {testimonials.map((testimonial, index) => (
            <Testimonial
              key={index}
              className="swiper-slide"
              {...testimonial}
            />
          ))}
        </div>
        <div className="swiper-pagination testimonials-pagination"></div>
      </div>
      <div
        className="swiper-button-prev testimonials-button-prev"
        css={{ left: -55 }}
      ></div>
      <div
        className="swiper-button-next testimonials-button-next"
        css={{ right: -55 }}
      ></div>
    </div>
  )
}

export default Testimonials
