import React, { useState, useEffect, useRef } from 'react'
import { message, Card, Icon, Tooltip, Modal } from 'antd'
import Swiper from 'swiper'
import 'swiper/dist/css/swiper.min.css'

import Space from '../components/Space'
import { Title } from '../styles/typography'

import { copyToClipboard } from '../utils'

import { Light as SyntaxHighlighter } from 'react-syntax-highlighter'
import json from 'react-syntax-highlighter/dist/esm/languages/hljs/json'
import codeStyle from 'react-syntax-highlighter/dist/esm/styles/hljs/atelier-lakeside-light'

SyntaxHighlighter.registerLanguage('json', json)

const getFormattedCode = code =>
  `<script type="application/ld+json">\n${JSON.stringify(
    code,
    null,
    2
  )}\n</script>`

const CodeExample = ({ title, description, code, className, ...props }) => {
  const [visible, setVisible] = useState(false)

  return (
    <>
      <Card
        actions={[
          <Tooltip placement="top" title="Show full code">
            <Icon type="zoom-in" onClick={() => setVisible(true)} />
          </Tooltip>,
          <Tooltip placement="top" title="Copy code">
            <Icon
              type="copy"
              onClick={() => {
                copyToClipboard(JSON.stringify(code, null, 2))
                message.success('Copied to clipboard!')
              }}
            />
          </Tooltip>,
        ]}
        className={className}
        css={{
          display: 'flex',
          flexDirection: 'column',
          height: 'auto',
          borderRadius: 6,
          '.ant-card-cover': {
            flex: 1,
            maxHeight: 250,
            cursor: 'pointer',
            position: 'relative',
            backgroundColor: '#ebf8ff',
            '&::after': {
              content: '""',
              display: 'block',
              width: '100%',
              height: 50,
              position: 'absolute',
              bottom: 0,
              left: 0,
              background: 'linear-gradient(transparent, #ebf8ff)',
              pointerEvents: 'none',
            },
          },
          '.ant-card-body': {
            flex: 0.5,
          },
          overflow: 'hidden',
        }}
        cover={
          <SyntaxHighlighter
            language="json"
            wrapLines={true}
            style={codeStyle}
            customStyle={{
              height: '100%',
              padding: 20,
              fontSize: 13,
              overflow: 'hidden',
            }}
            onClick={() => setVisible(true)}
          >
            {getFormattedCode(code)}
          </SyntaxHighlighter>
        }
        {...props}
      >
        <Card.Meta
          onClick={() => setVisible(true)}
          title={
            <Title as="h3" css={{ whiteSpace: 'normal' }}>
              {title}
            </Title>
          }
          description={description}
        />
      </Card>
      <Modal
        title={title}
        visible={visible}
        footer={null}
        width="auto"
        css={{
          maxWidth: 800,
        }}
        onCancel={() => setVisible(false)}
      >
        <p>{description}</p>
        <Space y={20} />
        <Card
          actions={[
            <div
              onClick={() => {
                copyToClipboard(getFormattedCode(code))
                message.success('Copied to clipboard!')
              }}
            >
              <Icon type="copy" /> Copy Code
            </div>,
          ]}
          css={{
            display: 'flex',
            flexDirection: 'column',
            '.ant-card-body': {
              display: 'none',
            },
            '.ant-card-cover': {
              flex: 1,
            },
            overflow: 'hidden',
          }}
          cover={
            <SyntaxHighlighter
              language="json"
              wrapLines={true}
              style={codeStyle}
              customStyle={{
                height: '100%',
                padding: 20,
                fontSize: 13,
              }}
            >
              {getFormattedCode(code)}
            </SyntaxHighlighter>
          }
        />
      </Modal>
    </>
  )
}

const CodeExamples = ({ codeExeamples }) => {
  const swiperContainer = useRef()

  useEffect(() => {
    const mySwiper = new Swiper(swiperContainer.current, {
      slidesPerView: 3,
      spaceBetween: 30,
      navigation: {
        nextEl: '.code-examples-button-next',
        prevEl: '.code-examples-button-prev',
      },
      pagination: {
        el: '.code-examples-pagination',
        clickable: true,
      },
      breakpoints: {
        600: {
          slidesPerView: 1,
        },
        900: {
          slidesPerView: 2,
        },
      },
    })

    return () => {
      mySwiper.destroy()
    }
  }, [])

  return (
    <div css={{ position: 'relative' }}>
      <div
        ref={swiperContainer}
        className="swiper-container"
        css={{ paddingBottom: 50 }}
      >
        <div className="swiper-wrapper" css={{ display: 'flex' }}>
          {codeExeamples.map((example, index) => (
            <CodeExample key={index} className="swiper-slide" {...example} />
          ))}
        </div>
        <div className="swiper-pagination code-examples-pagination"></div>
      </div>
      <div
        className="swiper-button-prev code-examples-button-prev"
        css={{ left: -55 }}
      ></div>
      <div
        className="swiper-button-next code-examples-button-next"
        css={{ right: -55 }}
      ></div>
    </div>
  )
}

export default CodeExamples
